<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-button-group&gt;\n      &lt;v-button type=\&quot;primary\&quot; icon=\&quot;plus-square\&quot; @click=\&quot;addTopNode\&quot; title=\&quot;\&quot;&gt;&lt;/v-button&gt;\n      &lt;v-button type=\&quot;primary\&quot; icon=\&quot;edit\&quot; :disabled=\&quot;!selectNode\&quot; @click=\&quot;modalVisible = true\&quot; title=\&quot;()\&quot;&gt;&lt;/v-button&gt;\n      &lt;v-button type=\&quot;primary\&quot; icon=\&quot;plus\&quot; :disabled=\&quot;!selectNode\&quot; @click=\&quot;addNode\&quot; title=\&quot;\&quot;&gt;&lt;/v-button&gt;\n      &lt;v-button type=\&quot;primary\&quot; icon=\&quot;close\&quot; :disabled=\&quot;!selectNode\&quot; @click=\&quot;delNode\&quot; title=\&quot;\&quot;&gt;&lt;/v-button&gt;\n  &lt;/v-button-group&gt;\n  &lt;v-tree :data=\&quot;editData\&quot; @select=\&quot;select\&quot; ref=\&quot;tree\&quot;&gt;&lt;/v-tree&gt;\n  &lt;v-modal title=\&quot;\&quot; :visible=\&quot;modalVisible\&quot; @ok=\&quot;changeTitle\&quot; @cancel=\&quot;modalVisible = false\&quot;&gt;\n    &lt;v-input placeholder=\&quot;\&quot; v-model=\&quot;newTitle\&quot;&gt;&lt;/v-input&gt;\n  &lt;/v-modal&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data() {\n    return {\n      selectNode: null,\n      modalVisible: false,\n      newTitle: '',\n      editData: [{\n        title: '0-0',\n        expanded: true,\n        children: [{\n          title: '0-0-0',\n          expanded: true,\n          children: [{\n            title: '0-0-0-0',\n          }, {\n            title: '0-0-0-1',\n          }]\n        }, {\n          title: '0-0-1',\n          children: [{\n            title: '0-0-1-0'\n          }]\n        }]\n      }, {\n        title: '0-1',\n        children: [{\n          title: '0-1-0'\n        }, {\n          title: '0-1-2'\n        }]\n      }, {\n        title: '0-2',\n      }],\n    }\n  },\n  methods: {\n    select(selectedNodes) {\n      this.selectNode = selectedNodes[0];\n    },\n    changeTitle() {\n      if (!this.newTitle) return this.$message.warning('');\n      this.$refs.tree.editNode(this.selectNode.clue, { title: this.newTitle });\n      this.newTitle = '';\n      this.modalVisible = false;\n    },\n    addTopNode() {\n      this.$refs.tree.addNode('0', {\n        title: ''\n      });\n    },\n    addNode() {\n      this.$refs.tree.addNode(this.selectNode.clue, {\n        title: `${this.selectNode.title}`\n      });\n    },\n    delNode() {\n      this.$refs.tree.delNode(this.selectNode.clue);\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
