<td>clue, data</td>
<td>-</td>
</tr>
<tr>
<td>delNode</td>
<td></td>
<td>clue</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="tree-events"><a class="header-anchor" href="#tree-events" aria-hidden="true"></a> Tree Events</h3>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>check</td>
<td></td>
<td>childrenArray</td>
</tr>
<tr>
<td>select</td>
<td></td>
<td>childrenArray</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      selectNode: null,
      modalVisible: false,
      newTitle: '',
      treeData: [{
        title: 'parent 1',
        expanded: true,
        selected: true,
        children: [{
          title: 'parent 1-0',
          expanded: true,
          disabled: true,
          children: [{
            title: 'leaf',
            disableCheckbox: true
          }, {
            title: 'leaf',
          }]
        }, {
          title: 'parent 1-1',
          checked: true,
          children: [{
            title: "<span style='color: #08c'>sss</span>"
          }]
        }]
      }],
      dragData: [{
        title: '0-0',
        expanded: true,
        children: [{
          title: '0-0-0',
          expanded: true,
          children: [{
            title: '0-0-0-0',
          }, {
            title: '0-0-0-1',
          }]
        }, {
          title: '0-0-1',
          children: [{
            title: '0-0-1-0'
          }]
        }]
      }, {
        title: '0-1',
        children: [{
          title: '0-1-0'
        }, {
          title: '0-1-2'
        }]
      }, {
        title: '0-2',
      }],
      editData: [{
        title: '0-0',
        expanded: true,
        children: [{
          title: '0-0-0',
          expanded: true,
          children: [{
            title: '0-0-0-0',
          }, {
            title: '0-0-0-1',
          }]
        }, {
          title: '0-0-1',
          children: [{
            title: '0-0-1-0'
          }]
        }]
      }, {
        title: '0-1',
        children: [{
          title: '0-1-0'
        }, {
          title: '0-1-2'
        }]
      }, {
        title: '0-2',
      }],
      asyncData: [
        { title: 'pNode 01', children: []},
        { title: 'pNode 02', children: []},
        { title: 'pNode 03'},
      ]
    }
  },
  methods: {
    selectFn(data) {
      console.log(data);
    },
    checkFn(data) {
      console.log(data);
    },
    select(selectedNodes) {
      this.selectNode = selectedNodes[0];
    },
    changeTitle() {
      if (!this.newTitle) return this.$message.warning('');
      this.$refs.tree.editNode(this.selectNode.clue, { title: this.newTitle });
      this.newTitle = '';
      this.modalVisible = false;
    },
    addTopNode() {
      this.$refs.tree.addNode('0', {
        title: ''
      });
    },
    addNode() {
      this.$refs.tree.addNode(this.selectNode.clue, {
        title: `${this.selectNode.title}`
      });
    },
    delNode() {
      this.$refs.tree.delNode(this.selectNode.clue);
    },
    getData(node) {
      console.log(node);
      return new Promise(resolve => {
        setTimeout(()=>{
          resolve([
            { title: `leaf ${node.clue}-0`},
            { title: `leaf ${node.clue}-1`},
            { title: `leaf ${node.clue}-2`},
          ])
        }, 1000)
      })
    },
  }
}
</script>


